import React, { Component } from 'react';
import { Col, Form, Input } from 'antd';
import { BtnSearch } from 'components/common/button';
import FilterPathEducate from 'components/common/input/FilterPathEducate/index';
import YearSemesterInput from 'components/common/input/YearSemesterInput';
import { updateQueryParams, getQueryParams } from 'utils/urlUtil';
import './style.scss';

@Form.create({ name: 'formTeacher' })
class FormFilterReward extends Component {
  state = {};
  componentDidMount = () => {
    const { form } = this.props;
    form.setFieldsValue(getQueryParams());
  };
  handleSearch = e => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      updateQueryParams(values);
    });
  };
  render() {
    const { getFieldDecorator } = this.props.form;
    const optionFieldDecorator = {
      trainTypeId: {
        rules: [
          {
            required: true,
            message: 'Vui lng chn H!',
          },
        ],
      },
      facultyId: {
        rules: [
          {
            required: true,
            message: 'Vui lng chn Khoa!',
          },
        ],
      },
      courseId: {
        rules: [
          {
            required: true,
            message: 'Vui lng chn Kha',
          },
        ],
      },
      studyFieldId: {
        rules: [
          {
            required: true,
            message: 'Vui lng chn CN',
          },
        ],
      },
      yearEduId: {
        rules: [
          {
            required: true,
            message: 'Vui lng chn Nm hc',
          },
        ],
      },
      semesterId: {
        rules: [
          {
            required: true,
            message: 'Vui lng chn Hc k',
          },
        ],
      },
    };
    return (
      <Form className="form-inline-label" layout="vertical" onSubmit={this.handleSearch}>
        <FilterPathEducate form={this.props.form} optionFieldDecorator={optionFieldDecorator} />
        <YearSemesterInput
          form={this.props.form}
          optionFieldDecorator={optionFieldDecorator}
          span={5}
        />
        <Col span={5}>
          <Form.Item label="M SV">{getFieldDecorator('studentCode')(<Input />)}</Form.Item>
        </Col>
        <Col span={5}>
          <Form.Item label="H tn">{getFieldDecorator('fullname')(<Input />)}</Form.Item>
        </Col>
        <Col span={2}>
          <div className="btn-group">
            <BtnSearch htmlType="submit" />
          </div>
        </Col>
      </Form>
    );
  }
}

export default FormFilterReward;



