import React, { Component, Fragment } from 'react';
import { Form, Col } from 'antd';
import { getTrainTypes } from 'services/trainType';
import { getFacultys } from 'services/faculty';
import { getCourses } from 'services/course';
import { getStudyFields } from 'services/StudyField';
import { getClasss } from 'services/class';
import { SelectAjax, SelectMultiAjax } from '../../index';

export default class FilterPathEducate extends Component {
  state = {
    widthField: 5,
  };

  componentWillMount() {
    const { column } = this.props;
    if (column) {
      this.setState({ widthField: column === true ? 24 : column });
    }
  }

  componentDidMount() {
    if (this.props.onReadyState) this.props.onReadyState();
  }

  render() {
    const { optionFieldDecorator = {}, ignoreParams = {}, componentFieldProps = {}, propsCustom = {} } = this.props;
    const { getFieldDecorator, setFieldsValue, getFieldValue } = this.props.form;
    const { classes, studyField, courseIds, hideFacultyId } = this.props;
    const trainTypeId = getFieldValue('trainTypeId');
    const facultyId = getFieldValue('facultyId');
    const courseId = getFieldValue('courseId');
    const studyFieldId = getFieldValue('studyFieldId');
    const { widthField } = this.state;
    const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 6 },
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 18 },
      },
    };

    let renderFormCommon;
    if (widthField === 24) {
      renderFormCommon = (
        <Fragment>
          <Form.Item {...formItemLayout} label="H">
            {getFieldDecorator('trainTypeId', optionFieldDecorator.trainTypeId)(
              <SelectAjax service={getTrainTypes} />
            )}
          </Form.Item>
          {!hideFacultyId ? (
            <Form.Item {...formItemLayout} label="Khoa">
              {getFieldDecorator('facultyId', optionFieldDecorator.facultyId)(
                <SelectAjax
                  setFieldsValue={setFieldsValue}
                  service={getFacultys}
                  params={ignoreParams.facultyId ? {} : { trainTypeId }}
                  allowGetData={ignoreParams.facultyId || !!trainTypeId}
                // open={!!trainTypeId && !facultyId}
                />
              )}
            </Form.Item>
          ) : null}
          {!courseIds ? (
            <Form.Item {...formItemLayout} label="Kho">
              {getFieldDecorator('courseId', optionFieldDecorator.courseId)(
                <SelectAjax
                  setFieldsValue={setFieldsValue}
                  service={getCourses}
                  params={ignoreParams.courseId ? {} : { trainTypeId, facultyId }}
                  allowGetData={ignoreParams.courseId || !!facultyId}
                />
              )}
            </Form.Item>
          ) :
            <Form.Item {...formItemLayout} label="Kho">
              {getFieldDecorator('courseIds', optionFieldDecorator.courseId)(
                <SelectMultiAjax
                  setFieldsValue={setFieldsValue}
                  service={getCourses}
                />
              )}
            </Form.Item>
          }
          {!studyField ? (
            <Form.Item {...formItemLayout} label="CN">
              {getFieldDecorator('studyFieldId', optionFieldDecorator.studyFieldId)(
                <SelectAjax
                  service={getStudyFields}
                  setFieldsValue={setFieldsValue}
                  params={ignoreParams.studyFieldId ? {} : { trainTypeId, facultyId, courseId }}
                  allowGetData={ignoreParams.studyFieldId || !!courseId}
                />
              )}
            </Form.Item>
          ) : null}
          {!classes ? (
            <Form.Item {...formItemLayout} label="Lp">
              {getFieldDecorator('classId', optionFieldDecorator.classId)(
                <SelectAjax
                  typeValue="int"
                  service={propsCustom.classId ? propsCustom.classId.service : getClasss}
                  setFieldsValue={setFieldsValue}
                  params={
                    ignoreParams.classId ? {} : { trainTypeId, facultyId, courseId, studyFieldId }
                  }
                  allowGetData={ignoreParams.classId || !!studyFieldId}
                  keyLabel={propsCustom.classId ? propsCustom.classId.keyLabel : "label"}
                  keyValue={propsCustom.classId ? propsCustom.classId.keyValue : "id"}
                // open={!!studyFieldId && !classId}
                />
              )}
            </Form.Item>
          ) : null}
        </Fragment>
      );
    } else {
      renderFormCommon = (
        <Fragment>
          <Col xs={24} sm={12} md={widthField}>
            <Form.Item label="H" {...formItemLayout}>
              {getFieldDecorator('trainTypeId', optionFieldDecorator.trainTypeId)(
                <SelectAjax service={getTrainTypes} {...componentFieldProps.trainTypeId} />
              )}
            </Form.Item>
          </Col>

          {!hideFacultyId ?
            (
              <Col xs={24} sm={12} md={widthField}>
                <Form.Item label="Khoa" {...formItemLayout}>
                  {getFieldDecorator('facultyId', optionFieldDecorator.facultyId)(
                    <SelectAjax
                      setFieldsValue={setFieldsValue}
                      service={getFacultys}
                      params={ignoreParams.facultyId ? {} : { trainTypeId }}
                      allowGetData={ignoreParams.facultyId || !!trainTypeId}
                      // open={!!trainTypeId && !facultyId}
                      {...componentFieldProps.facultyId}
                    />
                  )}
                </Form.Item>
              </Col>
            )
            :
            null
          }
          
          <Col xs={24} sm={12} md={widthField}>
            {!courseIds ? (
              <Form.Item {...formItemLayout} label="Kho">
                {getFieldDecorator('courseId', optionFieldDecorator.courseId)(
                  <SelectAjax
                    setFieldsValue={setFieldsValue}
                    service={getCourses}
                    params={ignoreParams.courseId ? {} : { trainTypeId, facultyId }}
                    allowGetData={ignoreParams.courseId || !!facultyId}
                    {...componentFieldProps.courseId}
                  />
                )}
              </Form.Item>
            ) :
              <Form.Item {...formItemLayout} label="Kho">
                {getFieldDecorator('courseIds', optionFieldDecorator.courseId)(
                  <SelectMultiAjax
                    setFieldsValue={setFieldsValue}
                    service={getCourses}
                  />
                )}
              </Form.Item>
            }
          </Col>

          {!studyField ?
            (
              <Col xs={24} sm={12} md={widthField}>
                <Form.Item label="CN">
                  {getFieldDecorator('studyFieldId', optionFieldDecorator.studyFieldId)(
                    <SelectAjax
                      service={getStudyFields}
                      setFieldsValue={setFieldsValue}
                      params={ignoreParams.studyFieldId ? {} : { trainTypeId, facultyId, courseId }}
                      allowGetData={ignoreParams.studyFieldId || !!courseId}
                      // open={!!courseId && !studyFieldId}
                      {...componentFieldProps.studyFieldId}
                    />
                  )}
                </Form.Item>
              </Col>
            )
            : null
          }

          {!classes ? (
            <Col xs={24} sm={12} md={4}>
              <Form.Item label="Lp">
                {getFieldDecorator('classId', optionFieldDecorator.classId)(
                  <SelectAjax
                    typeValue="int"
                    service={propsCustom.classId ? propsCustom.classId.service : getClasss}
                    setFieldsValue={setFieldsValue}
                    params={
                      ignoreParams.classId ? {} : { trainTypeId, facultyId, courseId, studyFieldId }
                    }
                    allowGetData={ignoreParams.classId || !!studyFieldId}
                    keyLabel={propsCustom.classId ? propsCustom.classId.keyLabel : "label"}
                    keyValue={propsCustom.classId ? propsCustom.classId.keyValue : "id"}
                    {...componentFieldProps.classId}
                  // open={!!studyFieldId && !classId}
                  />
                )}
              </Form.Item>
            </Col>
          ) : null}
        </Fragment>
      );
    }
    return <Fragment>{renderFormCommon}</Fragment>;
  }
}



