import React, { Component } from 'react';
import Page from 'components/LayoutComponents/Page';
import { Card, Row, Col } from 'antd';
import { Link } from 'react-router-dom';
import routeConstant from 'routes/route/routeConstant';
import { TableData, ColEditDelete } from 'components/common';
import { Btn, BtnAddNew } from 'components/common/button';
import { getQueryParams } from 'utils/urlUtil';
import { getListAddReward, exportDataAddReward } from 'services/AddReward';
import FormFilterReward from './FormFilterReward';

class AddRewardPage extends Component {
  state = {
    reload: 0,
    paramSearchClass: {
      semesterId: null,
    },
  };
  componentDidMount = () => {
    this.handleChangeQueryParams();
  };

  componentWillReceiveProps = () => {
    this.handleChangeQueryParams();
  };

  handleChangeQueryParams = () => {
    this.setState({ paramSearchClass: getQueryParams() });
  };

  exportAddReward = () => {
    const { paramSearchClass } = this.state;
    exportDataAddReward(paramSearchClass);
  };

  render() {
    const columns = [
      {
        title: 'M SV',
        dataIndex: 'code',
        key: 'code',
      },
      {
        title: 'H tn',
        dataIndex: 'fullname',
        key: 'fullname',
      },
      {
        title: 'Ngy sinh',
        dataIndex: 'dob',
        key: 'dob',
      },
      {
        title: 'Tn lp',
        dataIndex: 'className',
        key: 'className',
      },
      {
        title: 'S Q',
        dataIndex: 'approvalNumber',
        key: 'approvalNumber',
      },
      {
        title: 'Ngy Q',
        dataIndex: 'approvalDate',
        key: 'approvalDate',
      },
      {
        title: 'Cp khen thng',
        dataIndex: 'parentBonusTypeLabel',
        key: 'parentBonusTypeLabel',
      },
      {
        title: 'Hnh thc',
        dataIndex: 'description',
        key: 'description',
      },
      {
        title: 'Loi khen thng',
        dataIndex: 'bonusTypeLabel',
        key: 'bonusTypeLabel',
      },
      {
        title: 'Thao tc',
        dataIndex: 'operation',
        render: (text, record) => (
          <ColEditDelete
            linkEdit={{
              to: {
                pathname: routeConstant.KHUYEN_THUONG__NHAP_QDKT_CA_NHAN__NHAP_DU_LIEU,
                search: `?id=${record.approvalId}`,
              },
            }}
            onConfirmDelete={() => this.handleDelete(record.id)}
            hidenBtnDelete="true"
          />
        ),
      },
    ];
    const { reload, paramSearchClass } = this.state;
    return (
      <Card title="Nhp khen thng">
        <Row gutter={24}>
          <FormFilterReward />
        </Row>
        <Row>
          <Link to={routeConstant.KHUYEN_THUONG__NHAP_QDKT_CA_NHAN__NHAP_DU_LIEU}>
            <BtnAddNew />
          </Link>
          {paramSearchClass.semesterId ? (
            <Btn
              style={{ marginLeft: 20, height: 35 }}
              onClick={this.exportAddReward}
              icon="download"
            >
              Download
            </Btn>
          ) : null}
        </Row>
        <Row>
          <Col span={24}>
            <TableData
              reload={reload}
              formSearch
              service={getListAddReward}
              paramSearch={paramSearchClass}
              columns={columns}
              rowKey="id"
            />
          </Col>
        </Row>
      </Card>
    );
  }
}

export default props => (
  <Page {...props}>
    <AddRewardPage />
  </Page>
);



